.App {
  text-align: center;
  #quote-box {
    background-color: white;
    border-radius: 20px;
    padding: 1vh;
    max-width: 50%;
    min-width: 40%;
    min-height: 20%;
    #author {
      text-decoration: none;
    }
    text-shadow: 2px 0 rgba(231, 190, 27, 0.795), -2px 0 rgba(78, 182, 30, 0.74), 0 2px rgb(197, 94, 15), 0 -2px #14599ea3,
             1px 1px rgb(137, 30, 30), -1px -1px rgb(142, 26, 130), 1px -1px rgb(60, 1, 132), -1px 1px rgb(152, 189, 39);
    #tweet-quote {
      float: left;
      padding: 2%;
      font-size: 2vh;
      margin-left: 2vh;
      margin-bottom: 2vh;
      border-radius: 4px;
    }
    #new-quote {
      float: right;
      color: white;
      font-size: 2vh;
      margin-right: 2vh;
      padding: 2.1%;
      margin-bottom: 1vh;
      border-radius: 4px;
      border: none;
    }
  }
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    70% {
      transform: scale(.9);
    }
      100% {
      transform: scale(1);
    }
  }
  .button:hover{
      animation: pulse 1s infinite;
      transition: .3s
  }
  .programmerInfo {
    text-decoration: none;
    font-size: 2vh;
    margin-top: 1vh;
    transition: all .2s ease-in-out;
    font-style: italic;
  }
  .programmerInfo:hover{
    transform: scale(1.1);
  }
}



